import React, { useState } from 'react';
import Form from 'react-bootstrap/Form';
import { update } from '../BooksAPI';

const bookMenu = ({ bookInfo, handleChangeShelf }) => {
  const [shelf, setShelf] = useState(
    bookInfo.hasOwnProperty('shelf') ? bookInfo.shelf : undefined
  );

  const changeShelf = async (newShelf) => {
    await update(bookInfo, newShelf);
    setShelf(newShelf);
    handleChangeShelf(bookInfo.id, newShelf);
  };
  return (
    <div className='book-shelf-changer'>
      <Form.Select
        size='sm'
        aria-label='Default'
        style={{ backgroundColor: 'lightgray' }}
        onChange={(e) => {
          changeShelf(e.target.value);
        }}
      >
        <option value='0' disabled>
          Move to...
        </option>
        <option value='none' selected={shelf === 'none' || shelf === undefined}>
          None
        </option>
        <option
          value='currentlyReading'
          selected={shelf === 'currentlyReading'}
        >
          Currently Reading
        </option>
        <option value='wantToRead' selected={shelf === 'wantToRead'}>
          Want To Read
        </option>
        <option value='read' selected={shelf === 'read'}>
          Read
        </option>
      </Form.Select>
    </div>
  );
};

export default bookMenu;
