import './App.css';
import { Route, Routes } from 'react-router-dom';
import MyReads from './pages/myReads';
import Search from './pages/search';
import React, { useState, useEffect } from 'react';
import NavBar from './components/cmpntNavBar';
import { getAll } from './BooksAPI';

const App = () => {
  const [books, setBooks] = useState([]);

  const handleChangeShelf = (bookId, newShelf) => {
    const updatedBooks = books.map((b) => {
      if (b.id === bookId) {
        b.shelf = newShelf;
      }
      return b;
    });
    setBooks(updatedBooks);
  };
  useEffect(() => {
    const getBooks = async () => {
      await getAll()
        .then((res) => {
          if (res === undefined || res.length === 0) {
            setBooks([]);
          } else {
            setBooks(res);
          }
        })
        .catch((er) => setBooks([]));
    };
    getBooks();
  }, []);

  return (
    <div>
      <NavBar />
      <Routes>
        <Route
          exact
          path='/'
          element={
            <MyReads
              booksOnShelves={books}
              handleChangeShelf={handleChangeShelf}
            />
          }
        />
        <Route
          exact
          path='/search'
          element={<Search booksOnShelves={books} handleChangeShelf={handleChangeShelf} />}
        />
      </Routes>
    </div>
  );
};

export default App;
